#t-1-d {
    width: 100%;
}


#t-1-d h3 {
    color: rgb(131, 214, 255);
    text-align: center;
    font-size: 1.3rem;

}


.sep {
    height: 2vh;
}


.a-elm-d td:nth-child(2) {
    border: 2px solid orange;
    cursor: pointer;
    transition: background-color .3s;
}


.a-elm-d td {
    border-top: 2px solid orange;
    border-bottom: 2px solid orange;

}


.a-elm-d h4 {
    font-size: 1.3rem;
    color: orange;
}


.a-elm-d td:nth-child(1) {
    width: 90vw !important;
}

.a-elm-d td:nth-child(2) {
    width: 10% !important;
}


#t-1-d table {
    width: 100%;
    border-collapse: collapse;
}

#t-1-d td:nth-child(1),
#t-1-d td:nth-child(3) {
    width: 40%;
    vertical-align: top;
}

#t-1-d td:nth-child(2) {
    width: 20%;
    vertical-align: top;

}

.selected {
    background-color: rgb(131, 214, 255);
    
}
.no-selected {
    background-color: black;
    
}
.exp{
    width: 100%;
    background-color: rgba(255, 166, 0, 0.122) ;
    position: fixed;
    bottom: 0%;    
    padding: .5%;
    text-align: center;
}

.exp button{
    font-size: 1.3rem;
    width: 80%;
    background-color: rgb(131, 214, 255);
    
}

.foot{
    height: 20vh;

}